import { getLogin,getDoneTask } from '../../service/service.js'
    export default{
    	data(){
    		return{				
				doneList:[],
				mescroll:null,
				list:[]
    		}
    	},
    	methods:{
    		goDetail(tab){
				this.$router.push({path:"/finish/done_detail",query:{
					info:tab
				}})
			},
			upCallback(page) {
				var self = this;
				this.getListDataFromNet(page.num, page.size, function(curPageData) {
					console.log(page.num,page.size);
					if(page.num == 1) self.doneList = [];
					console.log(curPageData);
					self.doneList = self.doneList.concat(curPageData);
					self.mescroll.endSuccess(curPageData.length);
					console.log(self.doneList);
				
				}, function() {
					//联网失败的回调,隐藏下拉刷新和上拉加载的状态;
					self.mescroll.endErr();
				});
				// function getListDataFromNet(pageNum,pageSize,successCallback,errorCallback){
				// 	setTimeout(function () {
				// 		var data=self.list;
				// 		var listData=[];//模拟分页数据
				// 		for (var i = (pageNum-1)*pageSize; i < pageNum*pageSize; i++) {
				// 			if(i==data.length) break;
				// 			listData.push(data[i]);
				// 		}
				// 		console.log(listData);
						
				// 			successCallback&&successCallback(listData);//成功回调
						
						
						
				// 	},500)
				// }
				
				
			},
			downCallback(){
				var self = this;
				this.getListDataFromNet(1, 10, function(data){
					//联网成功的回调,隐藏下拉刷新的状态
					self.mescroll.endSuccess();
					//设置列表数据
					self.doneList = data;
				}, function(){
					//联网失败的回调,隐藏下拉刷新的状态
	                self.mescroll.endErr();
				});
				// function getListDataFromNet(pageNum,pageSize,successCallback,errorCallback){
				// 	setTimeout(function () {
				// 		var data=self.list;
				// 		var listData=[];//模拟分页数据
				// 		for (var i = (pageNum-1)*pageSize; i < pageNum*pageSize; i++) {
				// 			if(i==data.length) break;
				// 			listData.push(data[i]);
				// 		}
				// 		console.log(listData);
						
				// 			successCallback&&successCallback(listData);//成功回调
						
						
						
				// 	},500)
				// }
			},
			getListDataFromNet(pageNum,pageSize,successCallback,errorCallback){
				var self = this;
				setTimeout(function () {
					
					var data=self.list;
					
					var listData=[];//模拟分页数据
					for (var i = (pageNum-1)*pageSize; i < pageNum*pageSize; i++) {
						if(i==data.length) break;
						listData.push(data[i]);
					}
					console.log(listData);
					
						successCallback&&successCallback(listData);//成功回调
					
					
					
				},500)
			}
			
    	
		},
		created(){
			var token = localStorage.getItem("token");
				getDoneTask(token).then((res)=>{
					this.list = res.tableContent;
					//this.doneList =  res.tableContent.slice(0,10)
				})
			
		},
		mounted(){						
			var self = this;
			self.mescroll = new MeScroll("finish",{
				up:{
					isBounce:false,
					page:{
						num:0,
						size:10
					},
					callback: self.upCallback
				},
				down:{
					callback:self.downCallback
				}
			})
		}
					
	}	
    	
    



