
import { getListTask,getDetailInfo} from '../../service/service.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Icon from 'vux/src/components/icon/index.vue'


export default{
data(){
return{
infoList:[],
detailInfo:{},
show:false,
showToast: false,
message:""
}
},
components: {
Group,
Cell,
XDialog,
Icon
},
created(){
let code = this.$route.query.info.businessKey;
let token  = localStorage.getItem("token");

getListTask(code,token).then((result)=>{
this.infoList = result;


})
let time = new Date().getTime();
getDetailInfo(time,token,code).then((result)=>{
if(result.length>=1){
this.detailInfo = JSON.parse(result[0].json_data);
}
console.log(JSON.parse(result[0].json_data));
})
},
mounted(){
var self = this;
self.mescroll = new MeScroll("taskDetail",{
up:{
isBounce:false,
use:false
},
down:{
use:false
}
})




},
methods:{
getOption(index){
this.showToast = true;

}
}



}




